import uCharts from '../components/u-charts/u-charts.min.js';
var _self;
var canva=null;
let chartType;

export default {
	data() {
		return {
			index:'',
			title:'',
			kaoyanAnal:'',
			cWidth:'',
			cHeight:'',
			pixelRatio:1,
			serverData:'',
		}
	},
	onLoad(e) {
		_self = this;
		_self.title = e.name
		_self.index = e.index
		this.cWidth=uni.upx2px(750);
		this.cHeight=uni.upx2px(500);
		this.getServerData();
	},
	methods: {
		getServerData(){
			uni.request({
				url: 'http://47.97.213.36:7300/mock/5e145ffaf2ecc141ee032ab8/leader/kaoyanData',
				method:'POST',
				data:{
				},
				success: function(res) {
					/* console.log(res.data.data) */
					_self.serverData=res.data.data;
					let dataList ={categories:[],series:[]};
					if(_self.index == 1){
						 chartType = 'column'
						 dataList.categories = _self.serverData.kaoyanData.categories
						 dataList.series = _self.serverData.kaoyanData.series
						 console.log(_self.serverData.kaoyanData.dataAnal)
						 _self.kaoyanAnal = _self.serverData.kaoyanData.dataAnal.data
					}else if(_self.index == 2){
						 chartType = 'column'
						 dataList.categories = _self.serverData.kaoyanResult.categories
						 dataList.series = _self.serverData.kaoyanResult.series
						  _self.kaoyanAnal = _self.serverData.kaoyanResult.dataAnal.data
					}else if(_self.index == 3){
						 chartType = 'pie'
						 dataList.series = _self.serverData.kaoyanUnv.series
						  _self.kaoyanAnal = _self.serverData.kaoyanUnv.dataAnal.data
					}else if(_self.index == 4){
						chartType == 'pie'
						console.log(_self.serverData.kaoyanMajor)
						dataList.series = _self.serverData.kaoyanMajor.series
						 _self.kaoyanAnal = _self.serverData.kaoyanMajor.dataAnal.data
					}
					_self.show("canvas",dataList,chartType);
				},
				fail: () => {
					_self.tips="网络错误，小程序端请检查合法域名";
				},
			});
		},
		show(canvasId,chartData,chartType){
			if(chartType == 'pie'){
				canva = new uCharts({
					$this:_self,
					canvasId: canvasId,
					type:'pie',
					fontSize:11,
					legend:{show:true},
					background:'#FFFFFF',
					pixelRatio:_self.pixelRatio,
					series: chartData.series,
					animation: true,
					width: _self.cWidth*_self.pixelRatio,
					height: _self.cHeight*_self.pixelRatio,
					dataLabel: true,
					extra: {
						pie: {
						  lableWidth:15
						}
					},
				})
			}else if(chartType == 'column'){
				canva = new uCharts({
					$this:_self,
					canvasId: canvasId,
					type: chartType,
					legend:{show:true},
					fontSize:11,
					background:'#FFFFFF',
					pixelRatio:_self.pixelRatio,
					animation: true, 
					categories: chartData.categories,
					series: chartData.series,
					xAxis: {
						disableGrid:true,
					},
					yAxis: {
						//disabled:true
					},
					dataLabel: true,
					width: _self.cWidth*_self.pixelRatio,
					height: _self.cHeight*_self.pixelRatio,
					extra: {
						column: {
							type:'group',
							width: _self.cWidth*_self.pixelRatio*0.45/chartData.categories.length
						}
					  }
				});
			}

			
		},
		touch(e){
			canva.showToolTip(e, {
				format: function (item, category) {
					if(typeof item.data === 'object'){
						return category + ' ' + item.name + ':' + item.data.value 
					}else{
						return category + ' ' + item.name + ':' + item.data 
					}
				}
			});
		},
	}
}
